.prose ol {
  margin-bottom: 1rem;
  padding-left: 1.5rem;
}
.prose li {
  margin-bottom: 0.25rem;
  line-height: 1.7;
}
.prose li > ul,
.prose li > ol {
  margin-top: 0.25rem;
  margin-bottom: 0.25rem;
}
.prose a {
  color: #1a1a1a;
  text-decoration: underline;
  text-decoration-color: #999;
  text-underline-offset: 0.15em;
}
.prose a:hover {
  text-decoration-color: #1a1a1a;
}
