  background: #f5f5f5;
  padding: 0.15em 0.35em;
  border-radius: 3px;
}
.prose pre {
  background: #f5f5f5;
  padding: 1rem;
  border-radius: 4px;
  overflow-x: auto;
  margin-bottom: 1rem;
}
.prose pre code {
  background: none;
  padding: 0;
}
.prose hr {
  border: none;
  border-top: 1px solid #eee;
  margin: 2.5rem 0;
}
.prose blockquote {
  border-left: 3px solid #ddd;
  padding-left: 1rem;
  margin-left: 0;
  margin-bottom: 1rem;
  color: #555;
}
.prose table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 1rem;
  font-size: 0.95rem;
}
.prose th,
.prose td {
  padding: 0.5rem 0.75rem;
  text-align: left;
  border-bottom: 1px solid #eee;
}
.prose th {
  font-weight: 600;
  border-bottom: 2px solid #ddd;
}
.breadcrumb {
  font-size: 0.875rem;
  color: #666;
}
.breadcrumb a {
  color: #666;
  text-decoration: none;
}
.breadcrumb a:hover {
  color: #1a1a1a;
  text-decoration: underline;
}
.meta-text {
  font-size: 0.875rem;
  color: #666;
}
.content-container {
